@import '~reset-css/reset.css';

.wrap {
  width: 100%;
  font-family:-apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI","Noto Sans Japanese","ヒラギノ角ゴ ProN W3", Meiryo, sans-serif;
  
  .header {
    width: 100%;
    height: 100vh;
    background: #222;
    color:#fff;
    
    .header-ttl {
      position: absolute;
      top: 80px;
      left: 80px;
      
      .header-ttl-main {
        font-weight: bold;
        margin-bottom: 20px;
        
        .header-ttl-main-catch {
          font-size: 6rem;
          margin-bottom: 15px;
        }
        
        .header-ttl-main-exp {
          font-size: 0.7rem;
        }
      }
    }
    
    .header-sub {
      font-size: 0.9rem;
      line-height: 150%;
      letter-spacing: 0.1rem;
    }
    
    .header-navBtn {
      width: 80px;
      height: 80px;
      position: absolute;
      top: 0;
      right: 0;
      background: #000;
      cursor: pointer;
      transition: .3s;
      
      @mixin navBorder {
        width: 22px;
        position: absolute;
        left: 29px;
        display: block;
        content: '';
      }
      
      &:before {
        @include navBorder;
        border:none;
        background: #fff;
        height: 2px;
        top: 32px;
      }
      &:after {
        @include navBorder;
        border-top: solid 2px #fff;
        border-bottom: solid 2px #fff;
        height: 5px;
        top: 39px;
      }
      &:hover {
        background: #fff;
        
        &:before {
          background: #000;
        }
        
        &:after {
          border-color: #000;
        }
      }
    }
  }
  
  .main {
    
  }
  
  .footer {
    width: calc(100% - 80px);
    height: 175px;
    background: #222;
    color: #999;
    padding: 60px 0 0 80px;
    
    .footer-menu {
      font-size: 0.7rem;
      margin-bottom: 70px;
      
      .footer-menu-item {
        display: inline-block;
        transition: .3s;
        
        &:first-child {
          margin-right: 10px;
        }
        &:not(:first-child) {
          margin: 0 10px;
        }
        
        a {
          transition: .3s;
          text-decoration: none;
        }
        a:link { color: #999; }
        a:visited { color: #999; }
        a:hover { color: #fff; }
        a:active { color: #999; }
      }
    }
    
    .footer-ttl {
      font-weight: bold;
      font-size: 1.6rem;
      margin-bottom: 0.5rem;
    }
    
    .footer-copy {
      font-size: 0.8rem;
    }
  }
}



